*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter" sans-serif;
}

body{
  background: #E5E5E5;
}

section{
  padding: 45px 40px;
}

a{
  cursor: pointer;
}

hr{
  border: 1px solid #F5F5F5;
}

.container{
  width: 550px;
  height: 704px;
  margin:  auto;
}

.travel__navbar{
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F55A5A;
  font-family: 'Inter';
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: -0.075em;
  text-align: center;
  padding: 20px;
  color: #FFFFFF;
}

.travel__navbar p{
  display: block;

  margin-left: 5px;
}

.travel__card{
  background: #fff;
}

.card{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 36px 0;
}

.card__content{
  /* flex: 1; */
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 20px 40px 7px 20px;
  align-items: flex-start;
}

.card__location{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card__location p{
  font-family: 'Inter';
  font-weight: 600;
  font-size: 18px;
  line-height: 12px;
  letter-spacing: 0.17em;
  margin-right: 12px;
  margin-left: 4px;
  color: #2B283A;
}

.card__location p:last-child{
  font-weight: 400;
  font-size: 10.24px;
  line-height: 12px;
  text-decoration-line: underline;
  color: #918E9B;
}

.card__title{
  width: 326px;
  height: 40px;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #2B283A;
  margin: 4px 0 7px;
}

.card__date{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 9px;
}

.card__date p{
  font-weight: 700;
  font-size: 10.24px;
  line-height: 12px;
  color: #2B283A;
}

.card__date p:last-child{
  padding: 0 3px;
}


.card__text{  
  font-weight: 400;
  font-size: 10.24px;
  line-height: 150%;
  color: #2B283A;
}
